* {
  margin: 0;
  padding: 0;
}

@mixin h4_number {
  background-color: #5951ca;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
}


.h_box {
  margin: 20px auto 20px auto;
  text-align: center;
  width: 550px;
  padding: 0;

  #h2_1_wrap {
    margin: 0 auto;
    display: block;
    color: white;
    background-color: #5951ca;
    font-size: 20px;
    padding: 5px;
    font-weight: bold;
    border-radius: 30px 30px 0 0 ;
    opacity: 1;
  }

  #h2_2_wrap {
    margin: 0 auto;
    background-color: #F1F1EF;
    font-size: 15px;
    font-weight: bold;
    padding: 10px;
    border-radius: 0 0 30px 30px;
  }
}

.content_wrap {
  border: 1px solid;
  background-color: #282c34;
  padding: 30px 30px 0 30px;
  display: box;
  justify-content: center;
  margin: 0 auto;
  width: 1500px;
  height: 70px;
  background-color: #fdfeff;

  .header {
    div:nth-child(1) {
      border: 1px solid;
      margin-left: 35px;
      display: inline-block;
      width: 55%;
      background-color: grey;
      font-size: 30px;
      padding-top: 30px;

    }
      div:nth-child(2){
        display: inline;
        width: 40%;
        background-color: blue;
        font-size: 20px;
        padding: 3px;
      }

  }

  .crawling_wrap {

    .crawling_box {
      display: flex;
      justify-content: space-around;
      align-items: center;
      text-align: right;

      .box1 {
        width: 300px;
        height: 300px;
        background-color: #dad8ff;
        border-radius: 30px;
        margin: 10px;
      }
      .box2 {
        width: 300px;
        height: 300px;
        background-color: #ffefd8;
        border-radius: 30px;
        margin: 10px;
      }
      .box3 {
        width: 300px;
        height: 300px;
        background-color: #f8d8ff;
        border-radius: 30px;
        margin: 10px;
      }
      .box4 {
        width: 300px;
        height: 300px;
        background-color: #d8fff0;
        border-radius: 30px;
        margin: 10px;
      }

      .icon_box {
        display: flex;
        justify-content: space-between;
        .icon {
          display: inline-block;
          width: 100px;
          border-radius: 30px;
          padding: 15px;
        }

        h4 {
          @include h4_number;
        }

      }

      h5 {
        color: rgb(97, 97, 97);
      }

      @mixin data {
        display: inline-block;
        padding: 20px;
        font-size: 30px;
        color: #2a2294;
      }

      .crawling_blog_data {
        @include data;
        width: 270px;

        h5 {
          text-align: left;
          font-size : 20px;
          margin-right: 37px;
        }
        h1 {
          font-size: 75px;
        }
      }

      .crawling_place_data, .crawling_instagram_data,.crawling_cafe_data {
        @include data;
        width: 270px;
        h5 {
          font-size: 15px;
          text-align: left;
        }
        h2 {
          font-size: 40px;
        }
      }
    }
  }
}

.visit_wrap {
  width: 100%;
  height: 300px;
  background-color: rgba(45, 0, 128, 0.075);
  display: flex;
  justify-content: space-between;
  align-items: left;
  margin-top: 20px;

  h4 {
    @include h4_number;
    margin-top: 10px;
  }

  .day_of_week, .when {
    width: 100%;

    .icon_box {
      margin: 15px;
      color: #727272;
      display: flex;
      align-items: center;
      span {
        display: inline-block;
      }
    }
    .day_img_wrap,.when_img_wrap {
      width: 400px;
      display: flex;
      justify-content: right;
    }
  }
}

